import skrollr from '../../public/static/js/skrollr.min'
export const checkAgent = function () {
  var userAgentInfo = navigator.userAgent;
  var Agents = new Array("Android", "iPhone", "SymbianOS", "Windows Phone");
  var flag = true;
  for (var v = 0; v < Agents.length; v++) {
    if (userAgentInfo.indexOf(Agents[v]) > 0) {
      flag = false;
      break;
    }
  }
  return flag;
}
export const MixinSkrollr = {
  mounted() {
    this.$nextTick(() => {
      let flag = checkAgent()
      if (flag) {
        setTimeout(() => {
          skrollr.init()

        }, 500)
      } else {
        return false
      }

    })
  },
  destroyed() {
    let instance = skrollr.get()
    instance.destroy()
  }
}