<template>
  <div class="c-header">
    <div class="header-mask">
      <div class="header-mask__inner"
           v-if="isActive"
           v-html="$t(`nav.${Name}`)"></div>
    </div>
    <div class="toggle"
         data-toggle="modal"
         data-target="#myModal">
      <span class="glyphicon glyphicon-th"
            :class="[isShow ? 'active':'']"
            aria-hidden="true"></span>
    </div>
    <div class="language"
         @click="changeLanguage"
         v-html="$t('language.name')"></div>
    <nav class="navbar">
      <ul>
        <router-link to="Home"
                     class="navbar__item"
                     tag="li"
                     v-html="$t('nav.Home')"></router-link>
        <!-- <router-link to="About"
                     class="navbar__item"
                     tag="li">ABOUT</router-link> -->
        <router-link to="Skill"
                     class="navbar__item"
                     tag="li"
                     v-html="$t('nav.Skill')"></router-link>
        <router-link to="Project"
                     class="navbar__item"
                     tag="li"
                     v-html="$t('nav.Project')"></router-link>
        <router-link to="Discuss"
                     class="navbar__item"
                     tag="li"
                     v-html="$t('nav.Discuss')"></router-link>
        <li class="navbar__item"
            @click="push"
            v-html="$t('nav.Contact')"></li>
      </ul>
    </nav>
  </div>
</template>
<script>
export default {
  props: {
    Name: {
      type: String,
      default: ''
    }
  },
  watch: {
    Name() {
      this.isActive = false
      this.$nextTick(() => {
        this.isActive = true
      })
    }
  },
  data() {
    return {
      isShow: false,
      isActive: false
    }
  },
  mounted() {
    const _this = this
    $('#myModal').on('hidden.bs.modal', function(e) {
      _this.isShow = false
      _this.scrControl(1)
      _this.fontControl(0)
    })
    $('#myModal').on('show.bs.modal', function(e) {
      _this.isShow = true
      _this.scrControl(0)
      _this.fontControl(1)
    })
  },
  methods: {
    fontControl(t) {
      if (t == 1) {
        let font = document.querySelectorAll('.icon-box')
        for (let item of font) {
          item.classList.add('active')
        }
      } else {
        let font = document.querySelectorAll('.icon-box')
        for (let item of font) {
          item.classList.remove('active')
        }
      }
    },
    bodyScroll(event) {
      event.preventDefault()
    },
    scrControl(t) {
      if (t == 0) {
        //
        document.body.addEventListener('touchmove', this.bodyScroll, {
          passive: false
        })
      } else if (t == 1) {
        //
        document.body.removeEventListener('touchmove', this.bodyScroll, {
          passive: false
        })
      }
    },
    push() {
      let contact = document.querySelector('#contact')
      contact.scrollIntoView({ behavior: 'smooth' })
    },
    changeLanguage() {
      let localLanguage = localStorage.getItem('languageSet') || 'zh'
      if (localLanguage === 'zh') {
        localStorage.setItem('languageSet', 'en')
      } else {
        localStorage.setItem('languageSet', 'zh')
      }
      location.reload()
    }
  }
}
</script>
<style scoped lang="scss">
@keyframes header {
  0% {
    transform: translateY(50%);
  }
  100% {
    transform: translateY(0);
  }
}
.c-header {
  // height: 100px;
  // background: #000;
  // height: 667px;
  height: 706px;
  background-size: cover;
  background-image: url('../../public/static/img/suppages_header_about.jpg');
  width: 100%;
  position: relative;
  background-position: center center;
  background-attachment: fixed;
  display: table;
  .header-mask {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    width: 100%;
    background: rgba(0, 0, 0, 0.4);
    &__inner {
      font-family: Trade Gothic W1;
      padding: 0 2%;
      overflow: hidden;
      font-size: 140px;
      letter-spacing: 10px;
      color: #fff;
      font-weight: 700;
      animation: header 0.4s;
      /deep/ p {
        margin: 0;
        &:lang(zh) {
          font-size: 70px;
        }
      }
    }
  }
}
.language {
  font-family: Trade Gothic W01;
  position: absolute;
  height: 100px;
  line-height: 100px;
  right: 40px;
  top: 0px;
  color: #ffffff;
  cursor: pointer;
  z-index: 10004;
  // letter-spacing: 2px;
  font-size: 11px;
}

.toggle {
  margin: 38px;
  width: 24px;
  height: 24px;
  position: fixed;
  z-index: 10004;
  cursor: pointer;
  top: 0;
  left: 0;
  .glyphicon.glyphicon-th {
    font-size: 24px;
    color: #fff;
    transition: 0.4s;
  }
  .glyphicon.glyphicon-th.active {
    font-size: 16px;
    color: #715699;
    transition: 0.4s;
  }
}
.navbar {
  position: absolute;
  left: 0;
  right: 0;
  .navbar__item {
    display: inline-block;
    width: 100px;
    height: 100px;
    line-height: 100px;
    color: #fff;
    cursor: pointer;
    letter-spacing: 2px;
    font-size: 11px;
  }
}

@media screen and (max-width: 767px) and (min-width: 0px) {
  .navbar {
    display: none;
  }
  .c-header {
    height: 450px !important;
    background-position: 50%;
    background-repeat: no-repeat;
    background-attachment: scroll;
    .header-mask__inner {
      font-size: 42px;
      letter-spacing: 7px;
      /deep/ p:lang(zh) {
        font-size: 24px;
      }
    }
  }
}
</style>